<template>
  <div class="page trialpreson flex-v reviewerstatistics">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">复查人员统计</div>
      <div class="head-btn"
        v-if="btns.hasExport"
        data-btn-id='105005001'
        @click="exportFile"
        style="font-size:16px">
        <img src="@/assets/img/dsochuIcon.png"
          alt="导出">&nbsp;<span>导出</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        :model="queryObj"
        class="default-form-style"
        size='small'>
        <el-form-item label="登记日期：">
          <el-date-picker value-format='yyyy-MM-dd'
            v-model="dateRange"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="{ disabledDate: time => time > new Date() }"
            unlink-panels
            clearable></el-date-picker>
        </el-form-item>
        <el-form-item label="机构名称：">
          <el-select v-model="queryObj.createOrganizationId"
            placeholder="全部"
            clearable>
            <el-option :label="item.oname"
              v-for="item in orgList"
              :value="item.sysGuid"
              :key="item.sysGuid"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="重点类型："
          clearable>
          <el-select v-model="queryObj.personType">
            <el-option label="全部"
              value=""></el-option>
            <el-option label="普通居民"
              value="1"></el-option>
            <el-option label="儿童"
              value="2"></el-option>
            <el-option label="老年人"
              value="4"></el-option>
            <el-option label="孕产妇"
              value="8"></el-option>
            <el-option label="高血压"
              value="16"></el-option>
            <el-option label="肺结核"
              value="32"></el-option>
            <el-option label="2型糖尿病"
              value="64"></el-option>
            <el-option label="精神病"
              value="128"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="searchbtn "
            icon="el-icon-search"
            @click="getListResult"
            round>查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="tabledata padding20 flex-v"
      style="padding:5px 20px 20px 20px">
      <div class="table-box "
        style="padding:0;overflow:auto;flex:1">
        <el-table stripe
          ref="multipleTable"
          v-loading='loading'
          :data="tableData"
          tooltip-effect="dark"
          height='100%'
          header-cell-class-name='custom-table-cell'>
          <el-table-column type="index"
            width="50"
            label="序号">
          </el-table-column>
          <el-table-column align="center"
            label="重点类型">
            <template slot-scope="scope">
              <div>{{scope.row.personType | getBoxType}}</div>
            </template>
          </el-table-column>
          <el-table-column align="center"
            prop="countPerson"
            label="总人数">
          </el-table-column>
          <el-table-column align="center"
            label="操作"
            width="150">
            <template slot-scope="scope">
              <!--  v-if="btns.hasMore" data-btn-id='105005002' -->
              <a class="link"
                @click="morepage(scope.row)">
                更多
              </a>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
import { formatDate, exportFile, calcType } from '@/lib/utils'
import { xhrDownload } from '@/lib/xhr'
export default {
  name: 'reviewerstatistics',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      dateRange: '',
      queryObj: {
        personType: '',
        createOrganizationId,
        startCreateDate: '',
        endCreateDate: ''
      },
      tableData: [],
      loading: true
    }
  },

  created() {
    this.getListResult()
  },
  methods: {
    exportFile() {
      this.$confirm('', {
        title: '确定导出？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(() => {
          xhrDownload('/charts/base/exportReview', this.queryObj, 'arraybuffer').then(res => {
            exportFile(res.data, '复查人员统计')
          })
        })
        .catch(cancel => cancel)
    },

    morepage(row) {
      this.$router.push({
        name: 'reviewerstatisticsmore',
        query: {
          personType: row.personType,
          startRegisterDate: this.queryObj.startCreateDate,
          endRegisterDate: this.queryObj.endCreateDate
        }
      })
    },

    //查询机构
    organization() {
      let url = '/sys/org/selectThisOrgAllChildOrg'
      this.$post(url)
        .then(res => {
          this.organizationlist = res.data
        })
        .catch(error => {
          error && this.$message.error(error)
        })
    },

    //获取数据
    getListResult() {
      let url = '/charts/base/getReviewUserType'
      this.$post(url, this.queryObj)
        .then(res => {
          this.loading = false
          this.tableData = res.data.list
        })
        .catch(error => {
          this.loading = false
          error && this.$message.error(error)
        })
    }
  },

  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasExport: ~btnIds.indexOf('105005001'), // 登记按钮权限
        hasMore: ~btnIds.indexOf('105005002') // 修改按钮权限
      }
    },
    orgList() {
      return this.$store.state.orgList
    }
  },
  filters: {
    getBoxType: function(val) {
      let { nameList } = calcType(val)
      return nameList ? nameList.join('/') : '未分配'
    }
  },

  watch: {
    dateRange(val) {
      let start = val ? `${val[0]} 00:00:00` : ''
      let end = val ? `${val[1]} 23:59:59` : ''
      this.queryObj.startCreateDate = start
      this.queryObj.endCreateDate = end
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
    }
  }
}
.reviewerstatistics {
  .padding20 {
    padding: 20px;
  }
  .headertitle {
    align-items: center;
    justify-content: space-between;
  }
  .rightbtn {
    display: flex;
    .marginright50 {
      margin-right: 50px;
    }
    div {
      cursor: pointer;
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #666666;
      span {
        width: 25px;
        height: 60px;
      }
    }
  }
  .countlist {
    margin-bottom: 20px;
    .countlist-item {
      width: 300px;
      height: 118px;
      box-shadow: 0px 3px 10px 0px rgba(12, 52, 104, 0.15);
      border-radius: 6px;
      justify-content: center;
      flex-direction: column;
      padding: 0 30px;
      margin-right: 20px;
      .itemtitle {
        font-size: 14px;
      }
      .itemcount {
        font-size: 36px;
        font-weight: 700;
      }
    }
    .countlist-item:last-child {
      margin-right: 20px;
    }
  }
}
</style>

